.ic__card-container {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  background-color: #fefefe;
  color: #8a8a8a;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  margin-bottom: 2em;
}

.ic__card-container:hover {
  color: #8a8a8a;
}

.ic__card-container .ic__card-photo {
  position: relative;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  width: calc(100% + 2em);
  height: 10em;
  margin: -1em 0 1em -1em;
  border-radius: 3px 3px 0 0;
}

.ic__card-container .ic__card-photo:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.4), transparent);
  opacity: 1;
}

.ic__card-container .ic__card-title {
  position: absolute;
  bottom: 0;
  padding: 0.5em 1em;
  z-index: 1;
}

.ic__card-container .ic__card-title h2 {
  font-size: 1.75em;
  color: #fefefe;
  margin: 0;
}

.ic__card-container .ic__card-details-item {
  display: flex;
  align-items: center;
  margin-bottom: 0.5em;
}

.ic__user-avatar {
  position: absolute;
  height: 3em;
  width: 3em;
  border: 2px solid #000000;
  border-radius: 50%;
  background-color: white;
  cursor: default;
  display: flex;
  justify-content: center;
  align-items: baseline;
}

.ic__user-avatar.top {
  top: -1rem;
}

@media screen and (max-width: 39.9375em) {
  .ic__user-avatar.top {
    top: 0.5rem;
  }
}

.ic__user-avatar.right {
  right: -1rem;
}

@media screen and (max-width: 39.9375em) {
  .ic__user-avatar.right {
    right: 0.5rem;
  }
}

.ic__user-without-avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  font-size: 1.5rem;
}

